.themeToggle {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  border-radius: 12px;
  border: none;
  background: var(--theme-glass-bg);
  backdrop-filter: var(--theme-glass-backdrop);
  -webkit-backdrop-filter: var(--theme-glass-backdrop);
  border: 1px solid var(--theme-glass-border);
  color: var(--theme-text-secondary);
  cursor: pointer;
  font-size: 0.875rem;
  font-weight: 500;
  transition: all 0.2s ease;
  -webkit-user-select: none;
     -moz-user-select: none;
          user-select: none;
}

.themeToggle:hover {
  background: var(--theme-glass-bg-hover);
  color: var(--theme-text-primary);
  transform: translateY(-1px);
  box-shadow: var(--theme-glass-shadow);
}

.themeToggle:active {
  transform: translateY(0);
}

.iconContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  height: 20px;
  transition: transform 0.3s ease;
}

.iconContainer.dark {
  transform: rotate(180deg);
}

.icon {
  width: 18px;
  height: 18px;
  transition: all 0.2s ease;
}

.label {
  font-size: 0.875rem;
  font-weight: 500;
  transition: color 0.2s ease;
}

.skeleton {
  width: 80px;
  height: 36px;
  border-radius: 12px;
  background: var(--theme-glass-bg);
  border: 1px solid var(--theme-glass-border);
}

/* Mobile responsive */
@media (max-width: 640px) {
  .themeToggle {
    padding: 0.5rem;
  }
  
  .label {
    display: none;
  }
} 